import MessageQueue from './queue';
import Data from './data';
import SegmentProvider from './segment-provider';
import AnalyticsListener from './listener';
import { ErrorMonitor } from './errorMonitor';
import Logger from './logger';
import {
  EVENT_PAGE,
  EVENT_IDENTIFY,
  EVENT_ACCOUNTCREATED,
  EVENT_DOWNLOADED,
  EVENT_ERROR,
  EVENT_SEARCHED,
  EVENT_CHECKOUTSTARTED,
  EVENT_ORDERCOMPLETED,
  EVENT_TRACK,
  EVENT_EXPERIMENTVIEWED,
  EVENT_PRODUCTLISTVIEWED,
  EVENT_SIGNEDIN,
  EVENT_INVITESENT,
  EVENT_PRODUCTSEARCHED,
  EVENT_SEMANTICCHECKOUTSTARTED,
  EVENT_SEMANTICORDERCOMPLETED,
  EVENT_PRODUCTADDEDTOWISHLIST,
  EVENT_SIGNEDUP,
  EVENT_CARTVIEWED,
  EVENT_PAYMENTINFOENTERED,
  EVENT_PRODUCTADDED,
  EVENT_PRODUCTREMOVED,
  isString,
  compact,
  defaults,
  defaultsDeep,
  pageDefaults
} from './util';

function eventNameFrom(data) {
  return compact([data.eventAction, data.pageSection, data.eventLabel]).join(
    '.'
  );
}

function interactionArgs(analyticsData, eventName, ...args) {
  if (!isString(eventName)) {
    args.unshift(eventName);
    eventName = undefined; // eslint-disable-line no-param-reassign
  }

  const data = analyticsData.overlay(...args, {
    eventCategory: 'userInteraction'
  });

  data.uiEventName = eventName || eventNameFrom(data);
  return [eventName || data.eventAction, data];
}

/**
 * if error is an Error object, `name`, `message`, and `stack` are
 * non-enumerable and thus discarded by merge/overlay.
 * since we don't want them discarded we explicitly destructure and
 * restructure them into a plain object
 */
function flattenedPropsWithNonEnumerables(error) {
  const { message, name, stack } = error;
  return Object.assign({ message, name, stack }, error);
}

export default class AnalyticsWrapper {
  constructor(options) {
    this.options = options || {};

    this.debugMode = this.options.debugMode;

    this.initialDataSet = false;

    this.data = new Data({});
    this.logger = Logger.getLogger(this.debugMode);
    this.listener = null;
    this.errorMonitor = null;

    this.messageQueue = new MessageQueue();
    this.provider = new SegmentProvider();

    if (typeof window !== 'undefined') {
      window.sstkAW = this;
    }
  }

  loadProvider(sourceId) {
    const myClass = this;
    return new Promise(resolve => {
      const id = sourceId || myClass.options.sourceId;
      if (id) {
        myClass.provider
          .load(id)
          .then(() => {
            if (myClass.options.tagCollectorUrl) {
              myClass.provider.setupTagCollector(
                myClass.options.tagCollectorUrl
              );
            }

            // only start message queue here if initial data sent is true
            if (myClass.initialDataSet) {
              myClass.messageQueue.startProcessing();
            }
            resolve(myClass);
          })
          .catch(err => {
            myClass.logger.error(`error starting message queue: ${err}`, true);
            resolve(myClass);
          });
      } else {
        myClass.logger.error(
          'unable to load AnalyticsWrapper due to missing source id',
          true
        );
        resolve(myClass);
      }
    });
  }

  /* eslint-disable no-param-reassign */
  setInitialData(data) {
    this.data.update(data || {});
    if (!this.initialDataSet) {
      this.messageQueue.messageQueue.forEach(item => {
        item.payload.data = defaultsDeep(item.payload.data, data);
      });

      if (this.provider.providerLoaded && !this.messageQueue.started) {
        this.messageQueue.startProcessing();
      }
    }
    /* eslint-enable no-param-reassign */

    this.initialDataSet = true;
  }

  setData(data) {
    this.data.update(data || {});
  }

  startListener(options) {
    if (this.listener) {
      return this.logger.warn('listener has already been started');
    }

    this.listener = new AnalyticsListener(options);
    this.listener.listen(this);
    return this;
  }

  enableErrorMonitor(options) {
    if (this.errorMonitor) {
      return this.logger.warn('Error monitor has already been started');
    }
    this.errorMonitor = new ErrorMonitor(options);
    this.errorMonitor.monitor(this);
    return this;
  }

  setTagCollectorUrl(tagCollectorUrl) {
    if (!this.provider.tagCollectorLoaded && this.provider.providerLoaded) {
      this.provider.setupTagCollector(tagCollectorUrl);
    } else {
      this.provider.tagCollectorUrl = tagCollectorUrl;
    }
    return this;
  }

  page(url, page) {
    let pageData = page;
    if (!pageData) {
      pageData = typeof url === 'object' ? url : {};
    }

    let data = this.data.update({ page: pageData });

    // if this is a page view and messageQueue has not been started, enrich
    // pageDefaults ourselves to ensure accuracy on edge-case loads
    if (
      !this.initialDataSet ||
      (this.options.deferLoadProvider && !this.provider.providerLoaded)
    ) {
      data = defaults(data, pageDefaults());
    }

    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_PAGE,
      {
        data
      }
    );
    return this;
  }

  identify(userId, traits, o) {
    const opt = o || {};
    const data = {
      userId,
      traits,
      opt
    };

    // trying to maintain backwards compatibility here
    if (typeof userId === 'object') {
      data.traits = userId;
      data.userId = userId.id;
    }
    // is_merged_identity should be set to false
    if (((data || {}).traits || {}).is_merged_identity) {
      data.traits.is_merged_identity = false;
    }

    if (!data.userId) {
      // try to grab the saved user id if missing
      const { user } = this.data.toJSON();
      if (user && user.id) {
        data.userId = user.id;
      } else {
        this.logger.error("identify not sent... missing 'id'");
        return this;
      }
    }

    const { visit } = this.data.toJSON();
    if (visit && visit.visitorId) data.opt.visitor_id = visit.visitorId;

    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_IDENTIFY,
      {
        data
      }
    );
    return this;
  }

  accountCreated(u = {}) {
    const data = this.data.update({ user: u });
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_ACCOUNTCREATED,
      {
        data
      }
    );
    return this;
  }

  downloaded(eventData = {}, searchContext = {}) {
    const data = this.data.overlay({ eventValue: eventData, searchContext });
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_DOWNLOADED,
      {
        data
      }
    );
    return this;
  }

  experiment(eventData = {}) {
    const data = this.data.overlay(eventData);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_EXPERIMENTVIEWED,
      {
        data
      }
    );
    return this;
  }

  searched(searchContext = {}) {
    const data = this.data.overlay({ searchContext });
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_SEARCHED,
      {
        data
      }
    );
    return this;
  }

  checkoutStarted(cart = {}) {
    if (cart.currencyCode) {
      const data = this.data.overlay({ ecommerce: cart });
      this.messageQueue.enqueue(
        this.provider.handleMessage.bind(this.provider),
        EVENT_CHECKOUTSTARTED,
        {
          data
        }
      );
      return this;
    }

    const data = this.data.overlay(cart);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_SEMANTICCHECKOUTSTARTED,
      {
        data
      }
    );
    return this;
  }

  orderCompleted(order = {}) {
    if (order.currencyCode) {
      const data = this.data.overlay({ ecommerce: order });
      this.messageQueue.enqueue(
        this.provider.handleMessage.bind(this.provider),
        EVENT_ORDERCOMPLETED,
        {
          data
        }
      );
      return this;
    }

    const data = this.data.overlay(order);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_SEMANTICORDERCOMPLETED,
      {
        data
      }
    );
    return this;
  }

  customEvent(name, data = {}) {
    const eventData = this.data.overlay(data);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_TRACK,
      {
        name,
        data: eventData
      }
    );
    return this;
  }

  gaEvent(name, data = {}) {
    this.logger.warn(
      'gaEvent should not be used, please use customEvent method instead'
    );

    const eventData = this.data.overlay(data, { name });

    eventData.eventCategory = data.eventAction;
    eventData.eventAction = data.eventLabel;
    delete eventData.eventLabel;
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_TRACK,
      {
        name: 'gaEvent',
        data: eventData
      }
    );
    return this;
  }

  error(error = {}) {
    const data = this.data.overlay({
      error: flattenedPropsWithNonEnumerables(error)
    });
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_ERROR,
      {
        name: 'Error',
        data
      }
    );
    return this;
  }

  /* User Interaction Events */

  click(eventName, data) {
    const [name, mergedData] = interactionArgs(this.data, eventName, data, {
      eventAction: 'click'
    });
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_TRACK,
      {
        name,
        data: mergedData
      }
    );
    return this;
  }

  change(eventName, data) {
    const [name, mergedData] = interactionArgs(this.data, eventName, data, {
      eventAction: 'change'
    });
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_TRACK,
      {
        name,
        data: mergedData
      }
    );
    return this;
  }

  hover(eventName, data) {
    const [name, mergedData] = interactionArgs(this.data, eventName, data, {
      eventAction: 'hover'
    });
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_TRACK,
      {
        name,
        data: mergedData
      }
    );
    return this;
  }

  submit(eventName, data) {
    const [name, mergedData] = interactionArgs(this.data, eventName, data, {
      eventAction: 'submit'
    });
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_TRACK,
      {
        name,
        data: mergedData
      }
    );
    return this;
  }
  /* Segment Semantic Events */

  productListViewed(eventData = {}) {
    const data = this.data.overlay(eventData);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_PRODUCTLISTVIEWED,
      {
        data
      }
    );
    return this;
  }

  signedIn(eventData = {}) {
    const data = this.data.overlay(eventData);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_SIGNEDIN,
      {
        data
      }
    );
    return this;
  }

  inviteSent(eventData = {}) {
    const data = this.data.overlay(eventData);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_INVITESENT,
      {
        data
      }
    );
    return this;
  }

  productSearched(eventData = {}) {
    const data = this.data.overlay(eventData);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_PRODUCTSEARCHED,
      {
        data
      }
    );
    return this;
  }

  productAddedToWishlist(eventData = {}) {
    const data = this.data.overlay(eventData);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_PRODUCTADDEDTOWISHLIST,
      {
        data
      }
    );
    return this;
  }

  signedUp(eventData = {}) {
    const data = this.data.overlay(eventData);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_SIGNEDUP,
      {
        data
      }
    );
    return this;
  }

  cartViewed(eventData = {}) {
    const data = this.data.overlay(eventData);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_CARTVIEWED,
      {
        data
      }
    );
    return this;
  }

  paymentInfoEntered(eventData = {}) {
    const data = this.data.overlay(eventData);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_PAYMENTINFOENTERED,
      {
        data
      }
    );
    return this;
  }

  productAdded(eventData = {}) {
    const data = this.data.overlay(eventData);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_PRODUCTADDED,
      {
        data
      }
    );
    return this;
  }

  productRemoved(eventData = {}) {
    const data = this.data.overlay(eventData);
    this.messageQueue.enqueue(
      this.provider.handleMessage.bind(this.provider),
      EVENT_PRODUCTREMOVED,
      {
        data
      }
    );
    return this;
  }
}
